define(['./Component'], function (Component) {
    'use strict';
    return Component.extend({
        defaults: {
            ...Component.prototype.defaults,
            type: 'cell',
            tagName: 'td',
            draggable: ['tr']
        }
    }, {
        isComponent(el) {
            let result = '';
            const tag = el.tagName;
            if (tag == 'TD' || tag == 'TH') {
                result = {
                    type: 'cell',
                    tagName: tag.toLowerCase()
                };
            }
            return result;
        }
    });
});